import Vue from 'vue';
import Vuex from 'vuex';
import userCookie from '@/untils/userCookies';

Vue.use(Vuex);

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== 'production',
  state: {
    collapsed: false,
    userInfo: userCookie.getUserCookies(),
    menuRoutes: [],
    categoryForm: {},
  },
  mutations: {
    changeCollapsed(state) {
      state.collapsed = !state.collapsed;
    },
    saveUserInfo(state, payload) {
      const {
        email, username, appkey, role,
      } = payload;
      state.userInfo = {
        email, username, appkey, role,
      };
    },
    logout(state) {
      state.userInfo = {
        username: '',
        email: '',
        appkey: '',
        role: '',
      };
    },
    changeMenuRoutes(state, routes) {
      state.menuRoutes = routes;
    },
    changeCategoryForm(state, payload) {
      state.categoryForm = payload;
    },
  },
  actions: {
    changeCollapsed(context) {
      context.commit('changeCollapsed');
    },
    saveUserInfo(context, payload) {
      context.commit('saveUserInfo', payload);
      userCookie.setCookies(payload);
    },
    logout(context) {
      context.commit('logout');
      userCookie.removeUserCookies();
    },
    changeMenuRoutes(context, routes) {
      context.commit('changeMenuRoutes', routes);
    },
    changeCategoryForm(context, payload) {
      context.commit('changeCategoryForm', payload);
    },
  },
  modules: {
  },
});
