import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '@/views/layout/Home.vue';
import Login from '@/views/layout/Login.vue';
import setRoutes from '@/untils/setRoutePermission';
import store from '@/store/index';
import Statistics from '@/views/page/Statistics.vue';

Vue.use(VueRouter);

const menuRoutesMap = [
  {
    path: '/Goods',
    name: 'Goods',
    meta: {
      title: '商品',
      icon: 'credit-card',
      hidden: false,
    },
    component: Home,
    children: [
      {
        path: '/GoodsAdd',
        name: 'GoodsAdd',
        meta: {
          title: '添加商品',
          icon: 'file-add',
          hidden: false,
        },
        component: () => import('@/views/page/GoodsAdd.vue'),
      },
      {
        path: 'Edit/:id',
        name: 'GoodsEdit',
        meta: {
          title: '编辑商品',
          icon: 'file-edit',
          hidden: true,
        },
        component: () => import('@/views/page/GoodsAdd.vue'),
      },
      {
        path: '/GoodsList',
        name: 'GoodsList',
        meta: {
          title: '商品列表',
          icon: 'database',
          hidden: false,
        },
        component: () => import('@/views/page/GoodsList.vue'),
      },

    ],

  },
  {
    path: '/GoodsSet',
    name: 'GoodsSet',
    component: Home,
    meta: {
      title: '类目管理',
      icon: 'home',
      hidden: false,
    },
    children: [
      {
        path: '/CategoryList',
        name: 'CategoryList',
        component: () => import('@/views/page/categoryList.vue'),
        meta: {
          title: '类目列表',
          icon: 'file-add',
          hidden: false,
        },
      },
    ],
  },

];
const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    redirect: 'Statistics',
    meta: {
      title: '首页',
      icon: 'home',
      hidden: false,
    },
    children: [
      {
        path: 'Statistics',
        name: 'Statistics',
        meta: {
          title: '统计',
          icon: 'line-chart',
          hidden: false,
        },
        component: Statistics,
      },
    ],
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login,
    meta: {
      title: '登录',
      icon: 'line-chart',
      hidden: true,
    },
  },
];

const router = new VueRouter({
  routes,
  mode: 'hash',
});
let isAddRoutes = true;
router.beforeEach((to, from, next) => {
  if (to.path !== '/Login') {
    if (store.state.userInfo.username && store.state.userInfo.email
      && store.state.userInfo.appkey && store.state.userInfo.role) {
      if (isAddRoutes) {
        const menuroutes = setRoutes(store.state.userInfo.role, menuRoutesMap);

        store.dispatch('changeMenuRoutes', routes.concat(menuroutes)).then(() => {
          router.addRoutes(menuroutes);
          next();
        });
        isAddRoutes = false;
      }

      return next();
    }
    return next('/Login');
  }
  return next();
});
export default router;
