const roleToRoute = {
  coustomer: ['Goods', 'GoodsAdd', 'GoodsList', 'GoodsEdit'],
  admin: ['Goods', 'GoodsAdd', 'GoodsList', 'GoodsSet', 'GoodsEdit', 'CategoryList'],
};
function setRoutes(role, routesMap) {
  const roleToRouteName = roleToRoute[role];
  const resultRoutes = routesMap.filter((item) => {
    const obj = item;
    if (roleToRouteName.indexOf(item.name) !== -1) {
      const { children } = obj;
      obj.children = children.filter((child) => roleToRouteName.indexOf(child.name) !== -1);
      return true;
    }
    return false;
  });
  return resultRoutes;
}
export default setRoutes;
