<template>
  <div :class="{ header: true, coll: this.$store.state.collapsed }">
    <a-button
      type="primary"
      style="margin-bottom: 16px"
      @click="toggleCollapsed"
    >
      <a-icon
        :type="this.$store.state.collapsed ? 'menu-unfold' : 'menu-fold'"
      />
    </a-button>
    <a-breadcrumb>
      <a-breadcrumb-item>{{
        this.$router.currentRoute.matched[0].meta.title
      }}</a-breadcrumb-item>
      <a-breadcrumb-item>
        <router-link :to="{name:this.$router.currentRoute.matched[1].name}">{{
          this.$router.currentRoute.matched[1].meta.title
        }}</router-link></a-breadcrumb-item
      >
    </a-breadcrumb>
    <ul class="user">
      <li class="user-info">
        欢迎,{{ this.$store.state.userInfo.username }}
        <a-icon type="down" />
      </li>
      <li class="logout" @click="logout">退出登录</li>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  methods: {
    toggleCollapsed() {
      this.$store.dispatch('changeCollapsed');
    },
    logout() {
      this.$store.dispatch('logout');
      this.$router.push({
        path: '/Login',
      });
    },
  },
};
</script>
